export default{
	name: 'pinglun-panel',
	data(){
		return{
			plCon:'',
			plShow:false,
			focusFlag:false
		}
	},
	onLoad() {
		
	},methods:{
		submitPl(i){
			 this.$emit('submitPl',this.plCon)
			 this.plCon='';
		},
		showPl(){
			this.plShow=true;

			this.focusFlag=true;

		},
		closePl(){
			this.plShow=false
		}
	}
	
}
